<template>
  <div class="TheTodoWrapper">
    <header>
      <h1>Todo</h1>
      <div class="todoControls">
        <st-button class="todoControlButton" @click.native="handleToggleAddNewTaskPanelVisible">+</st-button>
        <st-button class="todoControlButton" @click.native="handleSetTaskControlMode('delete')">-</st-button>
        <st-button class="todoControlButton" @click.native="handleSetTaskControlMode('arrange')">=</st-button>
      </div>
    </header>
    <div class="todoView">
      <TheAddNewTaskPanel v-if="isAddNewTaskPanelVisible" class="addTodoForm" />
      <div v-if="currentDateTasks" class="todoList">
        <TodoItem v-for="currentDateTask in currentDateTasks" :currentDateTask="currentDateTask" :key="currentDateTask.id" class="todoItem" />
      </div>
      <div v-else>
        <p class="todoMessagePanel">There is nothing to do today.</p>
      </div>
    </div>
  </div>
</template>

<script>
import TheAddNewTaskPanel from '@/components/Todo/TheAddNewTaskPanel.vue'
import TodoItem    from './TodoItem.vue'
import stButton from '@/components/Forms/stButton.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'TheTodo',
  components: {
    TheAddNewTaskPanel,
    TodoItem,
    stButton
  },
  computed: {
    ...mapGetters('todo', ['isAddNewTaskPanelVisible', 'currentDateTasks']),
    ...mapGetters(['currentYMD'])
  },
  methods: {
    ...mapActions('todo', ['toggleIsAddNewTaskPanelVisible', 'setTaskControlMode']),

    handleToggleAddNewTaskPanelVisible(){
      this.toggleIsAddNewTaskPanelVisible()
    },
    handleSetTaskControlMode(mode){
      this.setTaskControlMode(mode)
    },
  },
}
</script>

<style lang="scss" scoped>
.TheTodoWrapper {
  background: #ffffff;

  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    h1 {
      color: $text-primary;
      font-size: 24px;
      font-weight: 300;
    }

    .todoControls {
      display: flex;

      .todoControlButton {
        margin-left: 12px;
      }
    }
  }

  .todoView {
    height: 524px;

    .todoList {
      margin-top: 18px;
      height: inherit;
      overflow-y: scroll;

      &::-webkit-scrollbar {
        width: 6px;
      }
      &::-webkit-scrollbar-track {
        background-color: #dfdfdf;
      }
      &::-webkit-scrollbar-thumb {
        background-color: #bfbfbf;
      }


      .todoItem {
        margin-bottom: 20px;
      }
    }

    .todoMessagePanel {
      background-color: #f9f9f9;
      font-size: 14px;
      height: 8em;
      line-height: 8em;
      text-align: center;
      text-decoration: underline;
    }
  }
}
</style>