<template>
  <div class="calender">
    <CalenderWeekDay :days="days" />
    <CalenderWeekDate v-for="(week, index) in calenderDividedBy6Weeks" :key="index" :week="week" />
</div>
</template>

<script>
import CalenderWeekDay  from './CalenderWeekDay.vue'
import CalenderWeekDate from './CalenderWeekDate.vue'
import { mapGetters, mapActions } from 'vuex'

import Calender from './Calender.js'

export default {
  name: 'TheCalender',
  components: {
    CalenderWeekDay,
    CalenderWeekDate
  },
  data:() => ({
    days: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
  }),
  computed: {
    ...mapGetters('calender', ['currentYear', 'currentMonth', 'currentDate']),

    calenderDividedBy6Weeks(){
      const calender = new Calender(this.currentYear, this.currentMonth).calender
      return [
        calender.splice(0, 7),
        calender.splice(0, 7),
        calender.splice(0, 7),
        calender.splice(0, 7),
        calender.splice(0, 7),
        calender.splice(0, 7),
      ]
    },
  },
  methods: {
    ...mapActions('calender', ['setCurrentYear', 'setCurrentMonth', 'setCurrentYMD']),

    changeCurrentMonth(mode){
      if(mode === 'prev'){
        this.setCurrentMonth(this.currentMonth - 1)
      } else if(mode === 'next'){
        this.setCurrentMonth(this.currentMonth + 1)
      }

      if(this.currentMonth < 0){
        this.setCurrentYear(this.currentYear - 1)
        this.setCurrentMonth(11)
      } else if(this.currentMonth > 11){
        this.setCurrentYear(this.currentYear + 1)
        this.setCurrentMonth(0)
      }

      const currentYMD = `${this.currentYear}-${this.currentMonth}-${this.currentDate}`
      this.setCurrentYMD(currentYMD)
    },
  },
  filters: {
    fixJSMonthExp: val => val + 1,
    zeroPad: val => ("00" + val).slice(-2),
  }
}
</script>

<style lang="scss" scoped>
.calender {
  border-right: solid 1px rgba(112, 112, 112, 0.15);
  border-bottom: solid 1px rgba(112, 112, 112, 0.15);
}
</style>