<template>
  <div class="container">
    <article>
      <header>
        <h1>{{ currentYear }}/{{ currentMonth | fixJSMonthExp | zeroPad }}/{{ currentDate | zeroPad }}</h1>
        <div class="calenderControls">
          <st-button @click.native="changeCurrentMonth('prev')" class="calenderControlButton">&lt;</st-button>
          <st-button @click.native="changeCurrentMonth('next')" class="calenderControlButton">&gt;</st-button>
        </div>
        <div class="todoProgressGroup">
          <div v-if="currentDateTasks">
            <p> {{ currentDateTasksCompletedNum }} /  {{ currentDateTasksIncompleteNum }}</p>
          </div>
          <div v-else>
            <p></p>
          </div>
          <ProgressBar :progress="currentDateTasksProgress" />
        </div>
      </header>
      <div class="calender">
        <TheCalender :year="currentYear" :month="currentMonth" ref="calender" />
      </div>
    </article>
    <aside>
      <TheTodo />
    </aside>
  </div>
</template>

<script>
import TheCalender    from '@/components/Calender/TheCalender.vue'
import TheTodo        from '@/components/Todo/TheTodo.vue'
import ProgressBar    from '@/components/ProgressBar.vue'
import stButton       from '@/components/Forms/stButton.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'Home',
  components: {
    TheCalender,
    TheTodo,
    ProgressBar,
    stButton
  },
  computed: {
    ...mapGetters('calender',
      [
        'currentYear',
        'currentMonth',
        'currentDate'
      ]),
    ...mapGetters('todo', 
      [
        'currentDateTasks',
        'currentDateTasksProgress',
        'currentDateTasksCompletedNum',
        'currentDateTasksIncompleteNum'
      ]),
  },
  methods: {
    ...mapActions('calender', ['setCurrentYMD']),
    ...mapActions('todo', ['setSchedules']),

    changeCurrentMonth(mode){
      const payload = (mode === "prev")
        ? "prev" : (mode === "next") 
          ? "next"
          : new Error("Payload must be 'prev' or 'next'")
      this.$refs.calender.changeCurrentMonth(payload)
    }
  },
  filters: {
    fixJSMonthExp: val => val + 1,
    zeroPad: val => ("00" + val).slice(-2)
  },
  created(){
    const today = new Date()
    const [year, month, date] = [ today.getFullYear(), today.getMonth(), today.getDate()]
    this.setCurrentYMD(`${year}-${month}-${date}`)

    const schedules = localStorage.getItem('stripe-todo-schedules')
    this.setSchedules(JSON.parse(schedules))

    this.$store.subscribeAction({
      after: (action, state) => {
        const type = action.type
        if(type === "todo/addNewTask" || type === "todo/setTaskState" || type === "todo/deleteTask" || type === 'todo/rearrangeTask'){
          const schedules = JSON.stringify(state.todo.schedules)
          localStorage.setItem("stripe-todo-schedules", schedules)
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.container {
  display: flex;

  article {
    box-sizing: border-box;
    margin-top: 16px;
    padding: 0;
    padding-left: 42px;
    padding-right: 21px;
    width: 70%;
  }
  aside {
    background: #ffffff;
    box-sizing: border-box;
    padding-left: 21px;
    padding-right: 42px;
    height: 100%;
    width: 30%;
  }
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  h1 {
    color: $text-primary;
    font-size: 32px;
    font-weight: 300;
    margin: 0;
  }

  p {
    color: $text-secondary;
    font-size: 14px;
    margin: 0.5em 0;
  }

  .calenderControls {
    display: flex;

    .calenderControlButton {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .todoProgressGroup {
    flex: 1 1 100%;
  }

}
.calender {
  margin-top: 16px;
}
</style>