<template>
  <div class="progressBarWrapper">
    <div class="progressBar" :style="{'width': progress + '%'}"></div>
  </div>
</template>

<script>
export default {
  name: 'ProgressBar',
  props: {
    progress: Number
  },
}
</script>

<style lang="scss" scoped>
.progressBarWrapper {
  background-color: #eeeeee;
  height: 3px;

  .progressBar {
    background-color: #ffaa00;
    height: inherit;
    transition: width 0.5s ease-in-out;
  }
}
</style>