export default {
  namespaced: true,

  state: () =>  ({
    currentYear: 2020,
    currentMonth: 7,
    currentDate: 1,
    currentYMD: "2020-7-1",
  }),
  getters: {
    currentYear: state => state.currentYear,
    currentMonth: state => state.currentMonth,
    currentDate: state => state.currentDate,
    currentYMD: state => state.currentYMD,
    currentMonthCalender: state => state.currentMonthCalender
  },
  mutations: {
    currentYear: (state, payload) => state.currentYear = payload,
    currentMonth: (state, payload) => state.currentMonth = payload,
    currentDate: (state, payload) => state.currentDate = payload,
    currentYMD: (state, payload) => state.currentYMD = payload,
    currentMonthCalender: (state, payload) => state.currentMonthCalender = payload,
  },
  actions: {
    setCurrentYear: ({ commit }, payload) => commit('currentYear', payload),
    setCurrentMonth: ({ commit }, payload) => commit('currentMonth', payload),
    setCurrentDate: ({ commit }, payload) => commit('currentDate', payload),
    setCurrentMonthCalender: ({ commit }, payload) => commit('setCurrentMonthCalender', payload),

    setCurrentYMD({ commit }, payload) {
      const [year, month, date] = payload.split("-")

      commit('currentYear', parseInt(year))
      commit('currentMonth', parseInt(month))
      commit('currentDate', parseInt(date))
      commit('currentYMD', payload)
    }
  },
}