<template>
  <div
    :class="{'complete': currentDateTask.done }" class="TodoItemWrapper" 
    @mouseenter="isTodoMouseenter=true" @mouseleave="isTodoMouseenter=false"
  >
    <div class="todoItemContents">
      <h1>{{ currentDateTask.title }}</h1>
      <p v-html="currentDateTask.description"></p>
    </div>
    <div class="todoItemControls">
      <transition>
        <div v-if="taskControlMode === 'default' && isTodoMouseenter===true" key="toggleCompleteState" class="todoItemMenu complete">
          <span v-if="currentDateTask.done" @click="handleSetTaskIncomplete(currentDateTask.id)">Undo</span>
          <span v-else @click="handleSetTaskCompleted(currentDateTask.id)">Done</span>
        </div>
        <div v-else-if="taskControlMode==='delete'" @click="handleDeleteTask(currentDateTask.id)" key="delete" class="todoItemMenu delete">
          <span>Delete</span>
        </div>
        <div v-else-if="taskControlMode==='arrange'" key="arrange" class="todoItemMenu rearrange">
          <span @click="handleRearrangeTask(currentDateTask.id, 'prev')"></span>
          <span @click="handleRearrangeTask(currentDateTask.id, 'next')"></span>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex' 

export default {
  name: 'TodoItem',
  props: {
    currentDateTask: Object
  },
  computed: {
    ...mapGetters('todo', ['taskControlMode'])
  },
  data(){
    return {
      isTodoMouseenter: false
    }
  },
  methods: {
    ...mapActions('todo', ['setTaskCompleted', 'setTaskIncomplete', 'deleteTask', 'rearrangeTask']),

    handleSetTaskCompleted(id){
      this.setTaskCompleted(id)
    },
    handleSetTaskIncomplete(id){
      this.setTaskIncomplete(id)
    },
    handleDeleteTask(id){
      this.deleteTask(id)
    },
    handleRearrangeTask(id, mode){
      this.rearrangeTask({
        taskID: id,
        mode: mode
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.v-enter-active, .v-leave-active {
  transition: transform 0.35s;
  position: absolute;
}
.v-enter {
  transform: translateX(100%);
}
.v-leave-to {
  transform: translateX(100%)
}
</style>
<style lang="scss" scoped>
.TodoItemWrapper {
  background-color: $button-bg;
  padding: 0 18px;
  position: relative;
  overflow: hidden;

  &.complete {
    border-left: solid 3px $accent-color;
    padding-left: 15px;
  }

  .todoItemContents {
    h1 {
      color: $text-primary;
      font-size: 16px;
      font-weight: normal;
      margin: 0;
      padding: 12px 0 6px;
    }

    p {
      color: $text-assist;
      font-size: 14px;
      line-height: 2em;
      margin: 0;
      padding: 0;
      padding: 0 0 8px;
    }
  }

  .todoItemControls {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 33%;

    .todoItemMenu {
      transition: transform 0.35s, background-color 0.15s;
      height: 100%;
      width: 100%;

      span {
        color: $text-wprimary;
        display: flex;
        flex: 1 1 100%;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        height: 100%;
        width: 100%;
        text-transform: uppercase;
        user-select: none;
      }

      &.rearrange {
        span {
          height: 50%;
        }
      }
    }

    .complete {
      background-color: $accent-color;
      &:hover {
        background-color: darken($accent-color, 5%);
      }
    }
    .delete {
      background-color: $menu-bg-daunger;
      &:hover {
        background-color: darken($menu-bg-daunger, 5%);
      }
    }
    .rearrange {
      background-color: $menu-bg-rearrange;
      &:hover {
        background-color: darken($menu-bg-rearrange, 5%);
      }
    }
  }
}
</style>