export default {
  state: () => ({
    taskControlMode: 'default',
    isAddNewTaskPanelVisible: false,
  }),
  getters: {
    taskControlMode: state => state.taskControlMode,
    isAddNewTaskPanelVisible: state => state.isAddNewTaskPanelVisible,
  },
  mutations: {
    taskControlMode: (state, payload) => state.taskControlMode = payload,
    isAddNewTaskPanelVisible: (state, payload) => state.isAddNewTaskPanelVisible = payload,
  },
  actions: {
    toggleIsAddNewTaskPanelVisible: ({ commit, getters, dispatch }) => {
      commit('isAddNewTaskPanelVisible', !getters.isAddNewTaskPanelVisible)
      dispatch('setTaskControlMode', 'default')
    },
    setTaskControlMode: ({ commit, getters }, payload) => {
      commit('taskControlMode', (getters.taskControlMode === payload)
        ? 'default'
        : payload)
    }
  }
}