<template>
  <div class="TheAddTodoForm">
    <div class="formGroup">
      <label for="">Title</label>
      <st-input v-model="title" />
    </div>
    <div class="formGroup">
      <label for="">Description</label>
      <st-textarea v-model="description" />
    </div>
    <div class="addTodoFormControls">
      <PaperButton @click.native="handleCloseAddNewTaskPanel" class="paperButton">Cancel</PaperButton>
      <PaperButton @click.native="handleAddNewTask" class="paperButton">OK</PaperButton>
    </div>
  </div>
</template>

<script>
import stInput     from '@/components/Forms/stInput.vue'
import stTextarea  from '@/components/Forms/stTextarea.vue'
import PaperButton    from '@/components/Forms/PaperButton.vue'
import { mapActions, mapMutations } from 'vuex'

export default {
  name: 'TheAddTodoForm',
  components: {
    stInput,
    stTextarea,
    PaperButton
  },
  data(){
    return {
      title: "Example Title",
      description: "Example Description"
    }
  },
  methods: {
    ...mapMutations('todo', ['isAddNewTaskPanelVisible']),
    ...mapActions('todo', ['addNewTask']),

    handleCloseAddNewTaskPanel(){
      this.isAddNewTaskPanelVisible(false)
    },
    handleAddNewTask(){
      this.addNewTask({
        title: this.title,
        description: this.description,
        done: false
      })

      this.handleCloseAddNewTaskPanel()
    }
  }
}
</script>

<style lang="scss" scoped>
.TheAddTodoForm {
  background-color: $button-bg;
  padding: 8px 16px;

  .formGroup {
    margin-bottom: 9px;

    label {
      color: $text-secondary;
      display: block;
      font-size: 14px;
      text-transform: uppercase;
      padding: 9px 0 9px;
    }
  }

  .addTodoFormControls {
    display: flex;
    justify-content: flex-end;

    .paperButton {
      margin-left: 18px;
    }
  }
}

</style>