import UIModule from './ui.js'

export default {
  namespaced: true,
  
  state: () => ({
    currentDateTasks: null,
    schedules: [
      {
        date: '2020-7-1',
        tasks: [
          { id: 0, title: 'Test Data', description: 'There are 4 tasks to do today!', done: true },
          { id: 1, title: 'Done', description: 'Todo Description 1<br>Todo Description 2', done: true },
          { id: 2, title: 'Done', description: 'Todo Description 1<br>Todo Description 2', done: true },
          { id: 3, title: 'Incomplete', description: 'Todo Description 1<br>Todo Description 2', done: false },
          { id: 4, title: 'Incomplete', description: 'Todo Description 1<br>Todo Description 2', done: false },
        ]
      },
      {
        date: '2020-7-23',
        tasks: [
          { id: 0, title: 'Calender', description: 'TheCalenderCalender', done: true},
          { id: 1, title: 'CalenderDate', description: 'CalenderCalenderDate', done: true}
        ]
      },
      {
        date: '2020-7-24',
        tasks: [
          { id: 0, title: 'Todo', description: 'aass', done: true }
        ]
      },
      {
        date: '2020-8-5',
        tasks: [
          { id: 0, title: '',   description: ' ',  done: true },
          { id: 1, title: 'Todo',        description: '97',                       done: true },
          { id: 2, title: 'Todo',        description: '9',               done: true },
          { id: 3, title: 'Todo', description: '930',             done: true },
          { id: 4, title: 'Todo',      description: 'D&D<br>10', done: false },
          { id: 5, title: 'Todo',        description: 'LocalStorage<br>10', done: false },
        ]
      },
      {
        date: '2020-9-3',
        tasks: [
          { id: 0, title: 'Todoid', description: 'Todoid', done: false },
          { id: 1, title: 'Todo',       description: '9', done: false },
          { id: 2, title: 'Todo',         description: '9<br>todo/schedulesLocalStorage', done: false },
        ]
      }
    ]
  }),
  getters: {
    schedules: state => state.schedules,
    findScheduleByYMDDate: state => YMD => {
      const schedule = state.schedules.filter(schedule => schedule.date === YMD)
      return schedule[0] ? schedule[0] : null
    },
    findScheduleTasksByYMDDate: (state, getters) => YMD => {
      const schedule = getters.findScheduleByYMDDate(YMD)
      return schedule
        ? schedule.tasks
        : null
    },
    findScheduleIndexByYMDDate: state => YMD => state.schedules.findIndex(schedule => schedule.date === YMD),
    currentDateTasks(state, getters, rootGetters){
      const currentYMD = rootGetters.calender.currentYMD
      const currentDateSchedule = getters.findScheduleByYMDDate(currentYMD)
      return currentDateSchedule && currentDateSchedule.tasks.length
        ? currentDateSchedule.tasks
        : null
    },
    currentDateTasksCompletedNum(state, getters){
      const currentDateTasks = getters.currentDateTasks
      return currentDateTasks
        ? currentDateTasks.filter(task => task.done === true).length
        : 0
    },
    currentDateTasksIncompleteNum(state, getters){
      const currentDateTasks = getters.currentDateTasks
      return currentDateTasks
        ? currentDateTasks.filter(task => task.done === false).length
        : 0
    },
    currentDateTasksProgress(state, getters){
      const currentDateTasksNum = getters.currentDateTasks ? getters.currentDateTasks.length : 0
      const currentDateTasksCompletedNum = getters.currentDateTasksCompletedNum
      return currentDateTasksNum
        ? currentDateTasksCompletedNum / currentDateTasksNum * 100
        : 0
    }
  },
  mutations: {
    schedules: (state, payload) => state.schedules = payload
  },
  actions: {
    addNewTask({ commit, getters, rootGetters, dispatch }, payload){
      const newSchedules = getters.schedules
      const currentYMD = rootGetters['calender/currentYMD']
      const currentDateSchedule = getters.findScheduleByYMDDate(currentYMD)
      const currentDateScheduleIndex = getters.findScheduleIndexByYMDDate(currentYMD)
      const currentDateScheduleNewTaskID = currentDateSchedule && currentDateSchedule.tasks.length
        ? currentDateSchedule.tasks.reduce((acc, val) => acc.id > val.id ? acc : val).id + 1
        : 0

      const newTask = {
        id: currentDateScheduleNewTaskID,
        title: payload.title,
        description: payload.description,
        done: payload.done
      }

      if(currentDateSchedule)
        newSchedules[currentDateScheduleIndex].tasks.push(newTask)
      else
        newSchedules.push({ date: currentYMD, tasks: [newTask]})

      commit('schedules', newSchedules)
      dispatch('reorderTaskID', currentYMD)
    },
    setTaskState({ commit, getters, rootGetters }, payload){
      const newSchedules = getters.schedules
      const currentYMD = rootGetters['calender/currentYMD']
      const targetScheduleTasks = getters.findScheduleTasksByYMDDate(currentYMD)
      const targetScheduleIndex = getters.findScheduleIndexByYMDDate(currentYMD)
      const targetScheduleTaskIndex = targetScheduleTasks.findIndex(task => task.id === payload.id)

      newSchedules[targetScheduleIndex].tasks[targetScheduleTaskIndex].done = payload.done
      commit('schedules', newSchedules)
    },
    setTaskCompleted({ dispatch }, id){
      dispatch('setTaskState', { id, done: true })
    },
    setTaskIncomplete({ dispatch }, id){
      dispatch('setTaskState', { id, done: false })
    },
    deleteTask({ commit, getters, rootGetters, dispatch }, taskID){
      const newSchedules = getters.schedules
      const currentYMD = rootGetters['calender/currentYMD']
      const currentDateScheduleIndex = getters.findScheduleIndexByYMDDate(currentYMD)
      const newCurrentDateTasks = getters.currentDateTasks.filter(task => task.id !== taskID)

      newSchedules[currentDateScheduleIndex].tasks = newCurrentDateTasks
      commit('schedules', newSchedules)

      if (!newSchedules[currentDateScheduleIndex].tasks.length) {
        dispatch('setTaskControlMode', 'default')
      }

      dispatch('reorderTaskID', currentYMD)
    },
    reorderTaskID({ commit, getters }, YMDDate){
      const newSchedules = getters.schedules
      const targetScheduleTasks = getters.findScheduleTasksByYMDDate(YMDDate)
      const targetScheduleIndex = getters.findScheduleIndexByYMDDate(YMDDate)
      const newScheduleTasks = targetScheduleTasks.map((task, index) => ({
        id: index,
        title: task.title,
        description: task.description,
        done: task.done
      }))

      newSchedules[targetScheduleIndex].tasks = newScheduleTasks
      commit('schedules', newSchedules)
    },
    setSchedules({ commit }, schedules){
      commit("schedules", schedules)
    },
    rearrangeTask({ commit, getters, rootGetters, dispatch }, payload){
      const newSchedules = getters.schedules
      const currentYMD = rootGetters['calender/currentYMD']
      const targetScheduleTasks = getters.findScheduleTasksByYMDDate(currentYMD)
      const targetScheduleIndex = getters.findScheduleIndexByYMDDate(currentYMD)
      
      const taskID = payload.taskID
      const targetTaskID = (payload.mode === 'prev')
        ? (taskID - 1 >= 0)
          ? taskID - 1
          : taskID
        : (taskID + 1 >= targetScheduleTasks.length)
          ? taskID
          : taskID + 1;

      [
        targetScheduleTasks[taskID],
        targetScheduleTasks[targetTaskID]
      ] = [
        targetScheduleTasks[targetTaskID],
         targetScheduleTasks[taskID]
      ]
      newSchedules[targetScheduleIndex].tasks == targetScheduleTasks

      commit('schedules', newSchedules)
      dispatch('reorderTaskID', currentYMD)
    }
  },
  modules: {
    ui: UIModule
  }
}