<template>
  <div class="calenderDate" @click="changeCurrentYMDFromCalender" :class="{currentMonthDate: isCurrentMonthDate}">
    <span class="dayCircle" :class="{selected: isDateSelected}">
      {{ dateObj.date }}
    </span>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'CalenderDate',
  props: {
    dateObj: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapGetters('calender', ['currentYear', 'currentMonth', 'currentDate', 'currentYMD']),

    isCurrentMonthDate(){
      const [year, month] = this.dateObj.ymd.split("-")
      return (this.currentYear == year && this.currentMonth == month)
        ? true : false
    },
    isDateSelected(){
      const [year, month, date] = this.dateObj.ymd.split("-")
      return (this.currentYear == year && this.currentMonth == month && this.currentDate == date)
        ? true : false
    }
  },
  methods: {
    ...mapActions('calender', ['setCurrentYMD']),

    changeCurrentYMDFromCalender(){
      this.setCurrentYMD(this.dateObj.ymd)
    }
  }
}
</script>

<style lang="scss" scoped>
.calenderDate {
  border: solid 1px rgba(112, 112, 112, 0.3);
  border-right: 0;
  border-bottom: 0;
  color: $text-secondary;
  display: flex;
  font-size: 16px;
  flex: 1;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  height: 65px;
  transition: background 0.15s;
  user-select: none;

  &:not(.currentMonthDate) {
    color: $text-assist;
    font-size: 14px;
  }
  &:hover {
    background-color: #f0f0f0;
  }

  .dayCircle {
    border-radius: 50%;
    display: block;
    height: 48px;
    line-height: 48px;
    width: 48px;
    text-align: center;
    transition: background 0.15s;
  }
  .dayCircle.selected {
    background-color: #ffaa00;
    color: #fff;
    transition: background 0.15s;
  }
}
</style>