import CalenderDate from "./CalenderDate.js"

export default class Calender {
  constructor(year, month){
    this.year = year
    this.month = month
    this.calender = null

    this.setCalender()
  }

  setCalender(){
    const previousMonthCalender = this.getPreviousMonthCalender()
    const currentMonthCalender  = this.getCurrentMonthCalender()
    const nextMonthCalender     = this.getNextMonthCalender()

    const currentMonthFirstDateDay = this.getCurrentMonthFirstDateDay()
    const calender = currentMonthCalender

    if(currentMonthFirstDateDay !== 0){
      calender.unshift(...previousMonthCalender.slice(-currentMonthFirstDateDay))
    }

    for(let day = 0, len = 42 - calender.length; len < 42; day++, len++){
      calender.push(nextMonthCalender[day])
    }

    this.calender = calender
  }

  getPreviousMonthCalender(){
    const previousMonth = this.month - 1

    return (previousMonth < 0)
      ? this.getCalender(this.year - 1, 11)
      : this.getCalender(this.year, previousMonth)
  }
  getCurrentMonthCalender(){
    return this.getCalender(this.year, this.month)
  }
  getNextMonthCalender(){
    const nextMonth = this.month + 1

    return (nextMonth > 11)
      ? this.getCalender(this.year + 1, 0)
      : this.getCalender(this.year, nextMonth)
  }
  getCalender(year, month){
    const lastDate = this.getMonthLastDate(year, month)
    const calender = []

    for(let date = 1; date <= lastDate; date++){
      calender.push(new CalenderDate(year, month, date))
    }

    return calender
  }
  getCurrentMonthFirstDateDay(){
    return new Date(this.year, this.month).getDay()
  }
  getMonthLastDate(year, month){
    return new Date(year, month + 1, 0).getDate()
  }
}